@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*,*::before,*::after{box-sizing:border-box;font-family:"Roboto",sans-serif}body{color:#fff;width:100%;height:100%;display:flex;justify-content:center;align-content:center;background-color:#3a3a3a}main{background-color:#4398c2;position:relative;width:400px;min-height:700px;margin:3rem;padding:2rem}h1,h2,h3,h4,h5,h6,p{-webkit-margin-after:0;margin-block-end:0;-webkit-margin-before:0;margin-block-start:0;font-weight:100}h1{font-size:6rem;margin:1.5rem}h2{font-size:2.5rem;margin-top:1rem}p{font-weight:300}.current-weather{display:flex;flex-direction:column;justify-content:center;align-content:center;text-align:center}.temp-min{margin:.5rem;color:rgba(255,255,255,.5)}.temp-max{margin:.5rem}ul{list-style-type:none;-webkit-margin-after:0;margin-block-end:0;-webkit-margin-before:0;margin-block-start:0;-webkit-padding-start:0;padding-inline-start:0}.inline-display{display:flex;justify-content:space-between;margin:.5rem 0}.weekday{width:5rem;text-align:left}input{background-color:rgba(255,255,255,.5);padding:.4em;border:none;height:2rem;margin:2rem 0}input:focus{outline:none}button{height:2rem;padding:0 1rem;border:none}header{position:absolute;top:0;left:0;width:100%;text-align:center;font-size:.8rem;padding:.1rem}footer{position:absolute;bottom:0;left:0;border-top:1px solid rgba(255,255,255,.2);width:100%;min-height:3rem;display:flex;justify-content:space-between;align-content:space-between}.icon{margin:0 1rem;color:rgba(255,255,255,.5)}.icon:hover{color:#fff}.hourly-weather{display:flex;overflow:scroll;border-top:1px solid rgba(255,255,255,.2);border-bottom:1px solid rgba(255,255,255,.2);width:100;margin-top:.1rem;padding-top:.3rem}::-webkit-scrollbar{width:0;height:.4rem}::-webkit-scrollbar-track{background:transparent}::-webkit-scrollbar-thumb{background:rgba(255,255,255,.2)}::-webkit-scrollbar-thumb:hover{background-color:#fff}.hourly-card{padding:.2rem .5rem .7rem;display:flex;flex-direction:column;align-items:center;justify-content:flex-end}.hourly-card>.weather-icon{padding-bottom:.2rem}.hourly-time{font-size:.7rem;white-space:nowrap;padding:.2rem .1rem 0}.weekday-today{padding:0 1rem}.hourly-precipitation{font-size:.7rem;color:rgba(255,255,255,.5);padding:.1rem 0;flex-grow:1}.hourly-temp{padding:.2rem 0}.column-container{display:flex;justify-content:space-between}
